import { SIGN_UP_SET_INITIAL_STATE, SIGN_UP_FAIL, SIGN_UP_UPDATE_AGREED_TERMS, USER_PROFILE_UPDATE } from 'actions/type';
import { ERROR_CODE_AUTH_EMAIL_IN_USE } from 'constants/firebaseErrorCode';

