import {
  SIGN_IN_SET_INITIAL_STATE,
  SIGN_IN_SUCCESS,
  SIGN_IN_FAIL,
  SESSION_CREATE,
  SESSION_REMOVE,
} from 'actions/type';
import { ERROR_CODE_AUTH_USER_NOT_FOUND, ERROR_CODE_AUTH_WRONG_PASSWORD } from 'constants/firebaseErrorCode';
import parseMessage from 'utils/error';


