import React from 'react';

import useGlobal from 'store';

// core components
import Wizard from 'components/Wizard/Wizard.jsx';
import GridContainer from 'components/Grid/GridContainer.jsx';
import GridItem from 'components/Grid/GridItem.jsx';

import { withFirebase } from 'pages/Firebase';
import Step1 from './WizardSteps/Step1.jsx';
import Step2 from './WizardSteps/Step2.jsx';
import Step3 from './WizardSteps/Step3.jsx';


const INITIAL_STATE = {
  first_name: '',
  first_nameState: 'success',
  last_name: '',
  last_nameState: 'success',
  email: '',
  emailState: 'success',
  dob: '',
  gender: '',
  street_name: '',
  street_no: '',
  city: '',
  country: '',
};

const WizardView = ({ firebase }) => {
  const [globalState, globalActions] = useGlobal();
  if (globalState.authUser) {
    // globalActions.user.updateUserProfile(INITIAL_STATE);
  }

  const {
    first_name,
    last_name,
    email,
    gender,
    dob,
    street_name,
    street_no,
    city,
    country,
    uid,
    profileImage,
  } = globalState.authUser;
  return (
    <GridContainer justify="center">
      <GridItem xs={12} sm={8}>
        <Wizard
          validate
          steps={[
            { stepName: 'About', stepComponent: withFirebase(Step1), stepId: 'about' },
            { stepName: 'Account', stepComponent: Step2, stepId: 'account' },
            { stepName: 'Address', stepComponent: Step3, stepId: 'address' },
          ]}
          title="Build Your Profile"
          subtitle="This information will let us know more about you."
          finishButtonClick={(e) => {
            const {
              about: {
                first_name,
                last_name,
                uid,
                profileImage,
              },
              account: { gender, dob, },
              address: {
                street_name,
                street_no,
                city,
                country,
              },
            } = e;
            const timestamp = firebase.fieldValue.serverTimestamp();
            firebase.user(uid).set(
              {
                first_name,
                last_name,
                dateUpdated: timestamp,
                gender,
                dob,
                street_name,
                street_no,
                city,
                country,
              },
              { merge: true },
            )
              .then(() => {
                alert('Information saved successfully');
                globalActions.user.updateUser({ first_name, last_name, gender, dob, street_name, street_no, city, country });
              });
          }}
          initialData={{
            about: {
              first_name,
              last_name,
              email,
              uid,
              profileImage,
            },
            account: {
              gender,
              dob,
            },
            address: {
              street_name,
              street_no,
              city,
              country,
            },
          }}
        />
      </GridItem>
    </GridContainer>
  );
};

export default withFirebase(WizardView);
